<template>
  <div class="notebook-content">
    <section>
      <div class="line"></div>
      <a class="notebook-img">
        <img src="//i8.mifile.cn/v1/a1/2627319b-cb4c-6c13-2c96-c18286c1f34e.webp" />
      </a>
    </section>
    <div class="small-line"></div>
    <div class="notebook">
      <a
        class="notebook-list"
        v-for="(item, index) in notebookMessage"
        :key="'li_' + index"
        @click="note(index)"
      >
        <div class="img">
          <img :src="item.NotebookPic" />
        </div>
        <div class="text">
          <h4 v-text="item.NotebookTitle"></h4>
          <p v-text="item.NotebookDesc"></p>
          <div class="money">
            <span class="active" v-text=" '￥'+item.Activeprice ">起</span>
            <s class="yuan" v-text="'￥' + item.price"></s>
          </div>
          <button>立即购买</button>
        </div>
      </a>
    </div>
    <!-- 更多 -->
    <div class="more">更多小米笔记本产品 ></div>

    <div class="line"></div>
    <a class="dry">
      <img
        src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/e960baf2834b8684e2149e3ef71c6f06.jpg?thumb=1&w=720&h=280"
      />
    </a>
  </div>
</template>
<script>
import "./style.scss";
export default {
  name: "notebookdetail",
  data() {
    name:'detail'
    return {
      notebookMessage: ""
    };
  },
  created() {
    this.$Axios.post("/v1/app/findNotebook").then(res => {
      this.notebookMessage = res.msg;
    });
  },
  methods: {
    note(index) {
      this.$router.push({
        name: "detail",
        params: this.notebookMessage[index]
      });

      localStorage.setItem('src', JSON.stringify(this.notebookMessage[index].NotebookPic));
      localStorage.setItem('desc', JSON.stringify(this.notebookMessage[index].NotebookDesc));
      localStorage.setItem('title', JSON.stringify(this.notebookMessage[index].NotebookTitle));
      localStorage.setItem('price', JSON.stringify(this.notebookMessage[index].price));
      localStorage.setItem('activeprice', JSON.stringify(this.notebookMessage[index].Activeprice));
    }
  }
};
</script>


