<template>
  <div class="index-wrap">
    <header>
      <!-- 搜索栏 -->
      <section class="search-content">
        <form>
          <div class="icon">
            <img class="left" src="../../assets/mi.png" />
          </div>
          <div class="input">
            <span class="iconfont icon-sousuo"></span>
            <input placeholder="搜索商品名称" />
          </div>
          <div class="login">
            <span class="iconfont icon-iconyonghu right"></span>
          </div>
        </form>
      </section>
      <!-- 导航栏 -->
      <Nav :fNav="this.navMessage"></Nav>
    </header>

    <div class="commponents" style="flex:1; padding: 78px 0 48px 0;">
      <!-- 轮播图 -->
      <Swiper :fSwiper="this.swiperMessage"></Swiper>
      <!-- 菜单 -->
      <Menu></Menu>
      <!--  手机-->
      <Iphone></Iphone>
      <!--电视 -->
      <Tv></Tv>
      <!-- 笔记本 -->
      <Notebook></Notebook>
      <!-- 家电 -->
      <Ha></Ha>
      <!-- 智能 -->
      <Int></Int>
    </div>
    <div class="return" @click="backtop" v-if="fun">
      <span class="iconfont icon-iconyonghu">返回</span>
    </div>
    <!-- 底部导航栏 -->
    <footer>
      <ul class="footer-list">
        <li
          v-for="(item, index) in footercontent"
          :key="'li_' + index"
          :class="{'active': index === currentIndex}"
          @click="fn(index)"
        >
          <span :class="item.className"></span>
          {{item.title}}
        </li>
      </ul>
    </footer>
  </div>
</template>
<script>
import "./style.scss";
import Nav from "../nav/index.vue";
import Swiper from "../swiper/index.vue";
import Menu from "../menu/index.vue";
import Iphone from "../iphone/index.vue";
import Tv from "../tv/index.vue";
import Notebook from "../notebook/index.vue";
import Ha from "../ha/index.vue";
import Int from "../int/index.vue";

export default {
  name: "index",
  // 定义子组件的标签名
  components: {
    Nav,
    Swiper,
    Menu,
    Iphone,
    Tv,
    Notebook,
    Ha,
    Int
  },
  data() {

    return {
      navMessage: [],
      swiperMessage: "",
      currentIndex: 0,
      footercontent: [
        {
          title: "首页",
          className: "iconfont icon-iconyonghu"
        },
        {
          title: "分类",
          className: "iconfont icon-iconyonghu"
        },
        {
          title: "购物车",
          className: "iconfont icon-iconyonghu"
        },
        {
          title: "我的",
          className: "iconfont icon-iconyonghu"
        }
      ],
      backshow: false,
      onTop: "",
      time: null
    };
  },
  methods: {
    fn(index) {
      this.currentIndex = index;
    },

    backtop() {
        this.time = setTimeout(() => {
            document.documentElement.scrollTop = document.body.scrollTop -= 100;
        }, 500);
      
    }
  },
  computed:{
    fun() {
      clearInterval(this.time);
    window.onscroll = function() {
      this.onTop =
        document.documentElement.scrollTop || document.body.scrollTop;
        console.log(this.onTop);
      if (this.onTop === 0) {
          clearTimeout(this.time);
          this.time = null;
   
      }
      if (this.onTop > 700) {
        this.backshow = true;
        return this.backshow
      }
    }
    }
  },
  mounted() {
     clearInterval(this.time);
    window.onscroll = function() {
      this.onTop =
        document.documentElement.scrollTop || document.body.scrollTop;
        console.log(this.onTop);
      if (this.onTop === 0) {
          clearTimeout(this.time);
          this.time = null;
   
      }
      if (this.onTop > 700) {
        this.backshow = true;
        console.log(this.backshow);
      }
      // } else {
      //   this.backshow = false;
      //     console.log(this.backshow);
      // }
    };

    //   请求导航栏数据
    this.$Axios.post("/v1/app/allNav").then(res => {
      this.navMessage = res.msg;
    });

    //   请求轮播图数据
    this.$Axios.post("/v1/app/findSwiper").then(res => {
      this.swiperMessage = res.msg;
    });
  },
// mounted() {
//     window.onscroll = function() {
//       this.onTop =
//         document.documentElement.scrollTop || document.body.scrollTop;
//         console.log(this.onTop);
//       if (this.onTop === 0) {
//           clearTimeout(this.time);
//           this.time = null;
   
//       }
//       if (this.onTop > 700) {
//         this.backshow = true;
        
//         console.log(this.backshow);
     
//       } else {
//         this.backshow = false;
//           console.log(this.backshow);
//       }
//     };
// }
};
</script>


