<template>
  <nav class="nav-content">
    <div class="nav">
      <section>
        <ul class="ul-nav">
          <li v-for="(item, index) in fNav" :key="'li_' + index">
            <span :class="{'now': currentIndex === index}" @click="fn(index)">{{item.title}}</span>
          </li>
        </ul>
      </section>
    </div>
    <div class="down" @click="btn">
      <span class="iconfont icon-xiala"></span>
    </div>
    <slot v-if="flag === true">
      <div class="down-list">
        <div class="down-content">
          <div class="all">
            <span class="quan">全部</span>
            <div class="down" @click="btn">
              <span class="iconfont icon-xiala"></span>
            </div>
          </div>
          <ul class="down-ul">
            <li
              class="down-li"
              v-for="(item, index) in fNav"
              :key="'li_' + index"
              v-text="item.title"
              @click="fn(index)"
              :class="{'now': currentIndex === index}"
            ></li>
          </ul>
        </div>
      </div>
    </slot>
  </nav>
</template>
<script>
import "./style.scss";
import BScroll from "better-scroll";
export default {
  props: ["fNav"],
  data() {
    return {
      currentIndex: 0,
      flag: false
    };
  },
  mounted() {
    new BScroll(".nav", {
      scrollX: true,
      click: true
    });
  },

  methods: {
    fn(index) {
      this.currentIndex = index;
    },
    btn() {
      this.flag = !this.flag;
      console.log(this.flag);
    }
  }
};
</script>


