<template>
  <div class="ha-content">
    <section>
      <div class="line"></div>
      <a class="ha-img">
        <img src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/807857511b118d96202527f63536f389.jpg?thumb=1&w=720&h=440" />
      </a>
    </section>
    <div class="small-line"></div>
    <div class="ha">
      <a class="ha-list" v-for="(item, index) in haMessage" :key="'li_' + index" @click="ha(index)">
        <div class="img">
          <img :src="item.HomePic" />
        </div>
        <div class="text">
          <h4 v-text="item.HomeTitle"></h4>
          <p v-text="item.HomeDesc"></p>
          <div class="money">
            <span class="active" v-text=" '￥'+item.Activeprice ">起</span>
            <s class="yuan" v-text="'￥' + item.price"></s>
          </div>
       
        </div>
      </a>
    </div>
    <!-- 更多 -->
    <div class="more">更多米家家电产品 ></div>

    <div class="line"></div>
    <a class="dry">
      <img
        src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/a56394373382e7d98a395b25c709cfcb.jpg?thumb=1&w=720&h=280"
      />
    </a>
  </div>
</template>
<script>
import "./style.scss";
export default {
  data() {
    name: 'detail'
    return {
      haMessage: ""
    };
  },
  created() {
    this.$Axios.post("/v1/app/findHome").then(res => {
      this.haMessage = res.msg;
     
    });
  },
  methods: {
    ha(index) {
    
      this.$router.push({
        name:'detail',
        params: this.haMessage[index]
      });
      localStorage.setItem('src', JSON.stringify(this.haMessage[index].HomePic));
      localStorage.setItem('desc', JSON.stringify(this.haMessage[index].HomeDesc));
      localStorage.setItem('title', JSON.stringify(this.haMessage[index].HomeTitle));
      localStorage.setItem('price', JSON.stringify(this.haMessage[index].price));
      localStorage.setItem('activeprice', JSON.stringify(this.haMessage[index].Activeprice));

 
      
      //  this.Message.title =JSON.parse(localStorage.getItem('title'));
      //  this.Message.price =JSON.parse(localStorage.getItem('price'));
      //  this.Message.activeprice =JSON.parse(localStorage.getItem('activeprice'));
      //  this.Message.src =JSON.parse(localStorage.getItem('src'));

    }
    
  }
};
</script>


