<template>
    <div class="menu-content">
        <section>
             <ul class="clearfix">
                 <li v-for="(item, index) in swiperMessage" :key="'' + index">
                     <img :src="item.menuPic"/>
                 </li>
             </ul>
        </section>
      
    </div>
</template>
<script>
import './style.scss';
export default {
    props: [],
    data() {
        return {
            swiperMessage:'',
        }
    },
    methods: {

    },
    created() {
         this.$Axios.post("/v1/app/findMenu").then(res => {
            this.swiperMessage = res.msg;
         });   
    },
 
}
</script>


