<template>
  <div class="commodity-content">
    <div class="content-top">
      <a class="left">
        <img
          src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/b230ae9b4bdd0ba08cd93fc1b7173dfa.jpg?thumb=1&w=358&h=508"
        />
      </a>
      <div class="right">
        <a class="right-top">
          <img src="//i8.mifile.cn/v1/a1/9aaa8c74-104f-cf3d-d2f4-bb6162973ed0.webp" />
        </a>
        <a class="right-bottom">
          <img
            src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/d239eda5e7e9283f1554a92eadf495d9.jpg?thumb=1&w=358&h=252"
          />
        </a>
      </div>
    </div>
    <div class="content-middle">
      <a class="middle-left">
        <img src="//i8.mifile.cn/v1/a1/139eeb1a-95ac-d278-4b1e-e2fb05798908.webp" />
      </a>
      <a class="middle-right">
        <img src="//i8.mifile.cn/v1/a1/1074a94c-0594-5ddd-7e53-2667d27e8e36.webp" />
      </a>
    </div>
    <div class="line"></div>
    <div class="content-bottom">
      <a class="xinxueqi">
        <img
          src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/5c2a299bf517d26ddcad038e21127850.jpg?thumb=1&w=720&h=280"
        />
      </a>
    </div>
    <div class="line"></div>
    <div class="choice">
      <a class="choice-choice">
        <img
          src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/93646c36119c9e1f18263eb888c7ca22.jpg?thumb=1&w=720&h=440"
        />
      </a>
    </div>
    <!-- 手机  -->
    <div class="small-line"></div>
    <div class="iphone">
      <a class="iphone-list" v-for="(item, index) in iphoneMessage" :key="'li_' + index" @click="rou(index)">
        <div class="img">
          <img :src="item.TelPic" />
        </div>
        <div class="text">
          <h4 v-text="item.TelTitle"></h4>
          <p v-text="item.TelDesc"></p>
          <div class="money" >
            <span class="active" v-text=" '￥'+item.Activeprice "> 起</span>
           
            <s class="yuan" v-text="'￥' + item.price"></s>
          </div>
          <button>立即购买</button>
        </div>
      </a>
     
    </div>
    <!-- 更多 -->
    <div class="more">
      更多小米手机产品 >
    </div>
    <div class="line"></div>
     <a class="dry">
        <img
          src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/d8d007dc22ccd528d0368351e1ba2600.jpg?thumb=1&w=720&h=280"
        />
      </a>
  </div>
</template>
<script>
import "./style.scss";
export default {
  name:'detail',
  data() {
    return {
       iphoneMessage: '',
   
    };
  },
  created() {
    this.$Axios.post('/v1/app/findTel').then( res=> {
       this.iphoneMessage = res.msg;
       
    })
  },
  methods: {
    
    rou(index) {
        if (this.$route.path !== "/detail") {
          this.$router.push({
            name: 'detail',
            params: this.iphoneMessage[index],
            
          });
          localStorage.setItem('src', JSON.stringify(this.iphoneMessage[index].TelPic));
          localStorage.setItem('desc', JSON.stringify(this.iphoneMessage[index].TelDesc));
          localStorage.setItem('title', JSON.stringify(this.iphoneMessage[index].TelTitle));
          localStorage.setItem('price', JSON.stringify(this.iphoneMessage[index].price));
          localStorage.setItem('activeprice', JSON.stringify(this.iphoneMessage[index].Activeprice));
        } 
    }
  }
};
</script>


