import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index/index.vue';
import Detail from '@/components/detail/index.vue';


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
   
    {
      path:'/detail',
      name: 'detail',
      component: Detail,
    
    }
  ]
})



