<template>
  <div class="banner-content">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, index) in fSwiper" :key="'li_' + index"><img :src= 'item.swiperPicUrl'/></div>
  
      </div>
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>
<script>
import Swiper from "swiper";
import './style.scss'

export default {

  props: ['fSwiper'],
  data() {
    return {};
  },
  mounted() {
    new Swiper(".swiper-container", {
      click: true,
      observer: true,
      pagination: {
        el: ".swiper-pagination",

      }
    });
  }
};

</script>


