<template>
  <div class="tv-content">
    <section>
      <div class="line"></div>
      <a class="tv-img">
        <img
          src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/19fd80ea70eaeb77625631716d8d19ea.jpg?thumb=1&w=720&h=440"
        />
      </a>
    </section>
    <div class="small-line"></div>
    <div class="tv">
      <a class="tv-list" v-for="(item, index) in tvMessage" :key="'li_' + index" @click="tv(index)">
        <div class="img">
          <img :src="item.TVPic" />
        </div>
        <div class="text">
          <h4 v-text="item.TVTitle"></h4>
          <p v-text="item.TVDesc"></p>
          <div class="money">
            <span class="active" v-text=" '￥'+item.Activeprice ">起</span>
            <s class="yuan" v-text="'￥' + item.price"></s>
          </div>
          <button>立即购买</button>
        </div>
      </a>
    </div>
    <!-- 更多 -->
    <div class="more">更多小米电视产品 ></div>
    <div class="line"></div>
    <a class="dry">
        <img
          src="//cdn.cnbj1.fds.api.mi-img.com/mi-mall/8823253975183bf99d1e85d444d05f23.jpg?thumb=1&w=720&h=280"
        />
      </a>
  </div>
</template>
<script>
import "./style.scss";
export default {
  name:'detail',
  data() {
    return {
      tvMessage: ""
    };
  },
  created() {
    this.$Axios.post("/v1/app/findTV").then(res => {
      this.tvMessage = res.msg;
    });
  },
   methods: {
    tv(index) {
        if (this.$route.path !== "/detail") {
          this.$router.push({
            name: 'detail',
            params: this.tvMessage[index],
            
          });
        localStorage.setItem('src', JSON.stringify(this.tvMessage[index].TVPic));
        localStorage.setItem('desc', JSON.stringify(this.tvMessage[index].TVDesc));
        localStorage.setItem('title', JSON.stringify(this.tvMessage[index].TVTitle));
        localStorage.setItem('price', JSON.stringify(this.tvMessage[index].price));
        localStorage.setItem('activeprice', JSON.stringify(this.tvMessage[index].Activeprice));
        } 
    }
  }
};
</script>



